<template>
  <div>
    <b-card class="border-top-0" style="background-image: linear-gradient(#78EC6C, #57E86B);">
      <h1 style="color: white">Cześć {{ this.getUserName }}</h1>
      <a
        style="font-family: Roboto; font-size: 20px; color: white; ">Losowanie obywa się o godzinie 15:00 oraz 18:00</a><br>
      <a style="font-family: Roboto; font-size: 20px; color: white">Jeśli poniżej pojawi się informacja, że wylosowałeś miejsce - potwierdź je!</a>
      <p v-if="getDrawnLotteryDrawId != -1" style="color: green; margin-top: 1%">Gratulacje, wylosowałeś miejsce nr:
          <b>{{ this.getParkingSpaceNumber }}</b> w dniu <b>{{ this.getDrawnParkingSpaceDate }}</b></p>
      <p v-if="getDrawnLotteryDrawId == -1" style="color: red; margin-top: 1%">
        Na ten moment, nie udało Ci się wylosować miejsca na jutro</p>
      <p v-if="!checkIfParkingConfirmed && getDrawnLotteryDrawId != -1" style="margin-top: 1%; color: red">
        Musisz jeszcze zatwierdzić swoje miejsce!</p>
      <b-button v-if="!checkIfParkingConfirmed && getDrawnLotteryDrawId != -1" type="submit" variant="success"
                class="mt-1"
                v-on:click="confirmDrawnParkingSpace">Zatwierdź miejsce
      </b-button>
      <b-button v-if="checkIfParkingConfirmed && getDrawnLotteryDrawId != -1" disabled class="mt-1"
                v-on:click="">Zatwierdzono
      </b-button>
    </b-card>
  </div>
</template>

<script>
  import axios from 'axios';

  export default {
    data() {
      return {
        username: this.$store.state.user.name,
        drawnLotteryId: -1,
        drawnParkingSpace: -1,
        drawnParkingSpaceDate: null,
        drawnParkingSpaceConfirmation: null,
        isParkingSpaceConfirmed: false
      }
    },
    computed: {
      getParkingSpaceNumber: function () {
        return this.drawnParkingSpace
      },
      getDrawnLotteryDrawId: function () {
        return this.drawnLotteryId
      },
      getDrawnParkingSpaceDate: function () {
        console.log(this.drawnParkingSpaceDate.split(' '))
        let splittedDate = this.drawnParkingSpaceDate.split(' ')
        return splittedDate[0] + ' ' + splittedDate[1] + ' ' + splittedDate[2] + ' ' + splittedDate[3]
      },
      getUserName: function () {
        return this.$store.state.user.name.split(' ')[0]
      },
      checkIfParkingConfirmed: function () {
        if (this.drawnParkingSpaceConfirmation != null) {
          return true
        }
        else {
          if (this.isParkingSpaceConfirmed) {
            return true
          }
          return false
        }
      }
    },
    methods: {
      checkIfUserDrawnParkingSpace: async function () {
        const lottery_draw_url = 'https://calie-parking-backend.herokuapp.com/lottery-draw'
        await axios.get(lottery_draw_url,
          {headers: {Authorization: 'Bearer ' + localStorage.getItem('auth_token')}})
          .then(response => {
            this.drawnParkingSpace = response.data.parking_space_number
            this.drawnLotteryId = response.data.lottery_draw_id
            this.drawnParkingSpaceDate = response.data.date
            this.drawnParkingSpaceConfirmation = response.data.confirmed_on
            console.log(response);
          })
          .catch(function (error) {
            console.log(error);
          });
      },
      confirmDrawnParkingSpace: function () {
        const lottery_draw_url = 'https://calie-parking-backend.herokuapp.com/lottery-draw/'
        axios.put(lottery_draw_url + this.drawnLotteryId, {},
          {headers: {Authorization: 'Bearer ' + localStorage.getItem('auth_token')}})
          .then(response => {
            alert("Miejsce zatwierdzone poprawnie")
            this.isParkingSpaceConfirmed = true
            console.log(response);
          })
          .catch(error => {
            alert("Niestety spóźniłeś się z rezerwacją")
            console.log(error);
          });
      }
    },
    created() {
      this.checkIfUserDrawnParkingSpace()
    }
  }
</script>



