<template>
  <div id="main-page">
    <b-card class="border-top-0" style="background-image: linear-gradient(#78EC6C, #57E86B);">
      <h1 style="color: white">Cześć  {{ this.name.split(' ')[0] }}</h1>
      <b style="color: white">Na tej stronie możesz zwolnić miejsce parkingowe lub wylosować nowe</b>
    </b-card>
  </div>
</template>
<script>
  import axios from 'axios'
  export default {
    data() {
      return {
        username: this.$store.state.user.username,
        name: this.$store.state.user.name,
      }
    },
    watch: {
      getName() {
        return this.name.split(' ')[0]
      },
    },
    methods: {
      getUserStatus: function () {
        const user_status_url = 'https://calie-parking-backend.herokuapp.com/user/status'
        axios({
          url: user_status_url, method: 'GET',
          headers: {Authorization: 'Bearer ' + localStorage.getItem('auth_token')}
        })
          .then(resp => {
            console.log("RESP_USER_STATUS")
            this.$store.state.user = resp.data
            this.username = resp.data.username
            this.name = resp.data.name
            console.log(resp.data.username)
          })
          .catch(err => {
            console.log("ERR")
          })
      }
    },
    mounted() {
      this.getUserStatus()
    }
  }
</script>
<style>
  #main-page {
    font-family: Roboto;
    font-size: 20px;
    background-image: linear-gradient(#78EC6C, #57E86B);
  }
</style>



