import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    status: '',
    auth_token: localStorage.getItem('auth_token') || '',
    user: {}
  },
  mutations: {
    auth_request(state){
      state.status = 'loading'
    },
    auth_success(state, token, user){
      state.status = 'success'
      state.auth_token = token
    },
    auth_error(state){
      state.status = 'error'
    },
    logout(state){
      state.status = ''
      state.auth_token = ''
    },
  },
  actions: {
    login({commit}, user){
      return new Promise((resolve, reject) => {
        const login_url = 'https://calie-parking-backend.herokuapp.com/auth/login'
        commit('auth_request')
        axios({url: login_url, data: user, method: 'POST'})
          .then(resp => {
            const auth_token = resp.data.auth_token
            localStorage.setItem('auth_token', auth_token)
            // Add the following line:
            axios.defaults.headers.common['Authorization'] = auth_token
            commit('auth_success', auth_token)
            resolve(resp)
          })
          .catch(err => {
            commit('auth_error')
            localStorage.removeItem('auth_token')
            reject(err)
          })
      })
    },
    register({commit}, user){
      return new Promise((resolve, reject) => {
        const register_url = 'https://calie-parking-backend.herokuapp.com/auth/register'
        commit('auth_request')
        axios({url: register_url, data: user, method: 'POST'})
          .then(resp => {
            const auth_token = resp.data.auth_token
            localStorage.setItem('auth_token', auth_token)
            // Add the following line:
            axios.defaults.headers.common['Authorization'] = auth_token
            // console.log(this.$store.getters.isLoggedIn)
            commit('auth_success', auth_token)
            resolve(resp)
          })
          .catch(err => {
            commit('auth_error', err)
            localStorage.removeItem('auth_token')
            reject(err)
          })
      })
    },
    logout({commit}){
      return new Promise((resolve, reject) => {
        commit('logout')
        localStorage.removeItem('auth_token')
        delete axios.defaults.headers.common['Authorization']
        resolve()
      })
    }
  },
  getters: {
    isLoggedIn: state => !!state.auth_token,
    authStatus: state => state.status,
    hasUserParking: state => state.user.owned_parking_space
  }
})



