<template>
  <!--pobierz zwolnione daty
  sprawdz czy wylosowałeś miejsce
  pobierz wygaśnięte miejsca
  zatwierdź wylosowane miejsca
  -->
  <div id="grad">
    <div>
      <h5>Cześć <b>{{ this.getName }}</b></h5>
      <h4>Tutaj możesz zwolnić swoje miejsce parkingowe nr: <b>{{ user_parking_space }}</b></h4>
    </div>
    <vc-date-picker
      class="center_calendar"
      v-model="today"
      :attributes='getAttributes'
      mode="multiple"
      color="green"
      :min-date="new Date()"
      :disabled-dates='[{ weekdays: [1, 7]}]'
      @dayclick="dayClicked"
      is-inline/>
    <!--//TODO: , {days: convertDateToDay}-->
    <b-button type="submit" variant="success" class="mt-1 mb-2"
              v-on:click="waiveParkingSpace(daysToWaive)">Zwolnij miejsce
    </b-button>
  </div>
</template>
<!--TODO: disabled-dates change to waivedParkingDates-->
<script>
  import axios from 'axios';

  export default {
    waivedParkingDates: [],
    data() {
      return {
        username: this.$store.state.user.name,
        user_parking_space: this.$store.state.user.owned_parking_space,
        today: [new Date()],
        selectedDay: null,
        daysToWaive: [],
        waivedParkingDates: [],
      };
    },
    computed: {
      getName() {
        return this.username.split(' ')[0]
      },
      getAttributes(){
        return [{dot: {color: 'red'}, dates: this.waivedParkingDates}]
      },
      convertDateToDay() {
        let days = []
        this.waivedParkingDates.map(elem => days.push(elem.charAt(elem.length - 2) == 0 ?
          parseInt(elem.slice(-1)) : parseInt(elem.slice(-2))))
        return days
      }
    },
    methods: {
      dayClicked: function (day) {
        if (!this.daysToWaive.includes(day['id']) && !this.waivedParkingDates.includes(day['id'])) {
          this.daysToWaive.push(day['id'])
        }
        else if (this.daysToWaive.includes(day['id'])) {
          let index = this.daysToWaive.indexOf(day['id'])
          this.daysToWaive.splice(index, 1)
        }
        this.selectedDay = day;
      },
      waiveParkingSpace: async function (dates) {
        const parking_space_url = 'https://calie-parking-backend.herokuapp.com/parking-space'
        axios.post(parking_space_url, {
          dates: dates
        }, {headers: {Authorization: 'Bearer ' + localStorage.getItem('auth_token')}})
          .then(response => {
            console.log("parking waived")
            alert('Miejsce zwolnione')
            this.$router.push('/')
          })
          .catch(function (error) {
            alert('Jedna z dat już jest zwolniona')
          });
        this.getWaivedParkingSpaces()
      },
      getWaivedParkingSpaces: async function () {
        const parking_space_url = 'https://calie-parking-backend.herokuapp.com/parking-space'
        axios.get(parking_space_url,
          {headers: {Authorization: 'Bearer ' + localStorage.getItem('auth_token')}})
          .then(response => {
            this.waivedParkingDates = response.data.dates
          })
          .catch(error => {
            console.log(error);
          });
      }

    },
    async created() {
      this.getWaivedParkingSpaces()
      console.log(this.waivedParkingDates)
    }
  };
</script>

<style>
  .center_calendar {
    padding-top: 1%;
    display: block;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
  }

  #grad {
    background-image: linear-gradient(#78EC6C, #57E86B);
  }

</style>




