<template>
  <div xmlns:v-slot="http://www.w3.org/1999/XSL/Transform">
    <b-navbar toggleable="lg" type="dark" class="navbar-custom">
      <b-navbar-brand style="font-family: Roboto; font-size: 25px;">CA parking app</b-navbar-brand>

      <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>

      <b-collapse id="nav-collapse" is-nav>
        <b-navbar-nav style="font-family: Roboto; font-size: 18px;">
          <b-nav-item>
            <router-link to="/login" class="nav-link" v-if="!isLoggedIn">Zaloguj się</router-link>
          </b-nav-item>
          <b-nav-item>
            <router-link to="/calendar" class="nav-link" v-if="isLoggedIn && hasUserParking">Kalendarz</router-link>
          </b-nav-item>
          <b-nav-item>
            <router-link to="/lottery" class="nav-link" v-if="isLoggedIn && !hasUserParking">Losowanie</router-link>
          </b-nav-item>
          <b-nav-item><span v-if="isLoggedIn" class="nav-link"> <a @click="logout">Wyloguj się</a></span></b-nav-item>
        </b-navbar-nav>
      </b-collapse>
    </b-navbar>
  </div>
</template>

<script>
  export default {
    computed: {
      isLoggedIn: function () {
        return this.$store.getters.isLoggedIn
      },
      hasUserParking: function () {
        return this.$store.getters.hasUserParking
      }
    },
    methods: {
      logout: function () {
        this.$store.dispatch('logout')
          .then(() => {
            this.$router.push('/login')
          })
      }
    },
    created: function () {
      this.$http.interceptors.response.use(undefined, function (err) {
        return new Promise(function (resolve, reject) {
          if (err.status === 401 && err.config && !err.config.__isRetryRequest) {
            this.$store.dispatch(logout)
          }
          throw err;
        });
      });
    }
  }
</script>
<style>
  .nav-link {
    color: white !important
  }

  .navbar-custom {
    color: #FFFFFF;
    /*background-color: #CC3333;*/
    background-image: linear-gradient(#A9F36A, #78EC6C);
  }
</style>



